<script>
import { useAccountStore, useTemplateStore } from '@/store'
import { mapState } from 'pinia'

import debug from 'debug'

import TemplateCanvas from '../../components/TemplateEditor/components/Canvas/Canvas.vue'
import MessageModal from '../../components/TemplateEditor/modals/MessageModal.vue'

import IconClose from '@/assets/icon-close.vue'
import IconFile from '@/assets/icon-file.vue'
import IconUpload from '@/assets/icon-upload.vue'
import MoreInfo from '@/assets/more-info.vue'
import CustomCard from '@/components/CustomCard'
import { DEFAULT_ACTIVE_COMPONENT } from '@/store/TemplateStore'
import { IMPORT_FILE, UPLOAD_DOC } from '@/store/actions'

const logDebug = debug('IssueInBulk')

export default {
  name: 'IssueInBulk',
  components: {
    IconClose,
    IconUpload,
    IconFile,
    CustomCard,
    MoreInfo,
    MessageModal,
    TemplateCanvas,
  },
  emits: ['close'],
  data() {
    return {
      step: 0,
      password: '',
      file: null,
      importingCredentials: false,
      headers: [],
      templateTable: [],
      templateValues: [],
      tableValues: [],
      showConfirmData: false,
      highlight: false,
      email: null,
      sending: false,
      reload: true,
      buttonText: this.$t('button.next'),
      errorMsg: false,

      templateCopy: null,

      showSuccessModal: false,
      popupMessage: 'storing',
      pages: ['#page'],

      customTemplate: { 'wallid.io': true },
    }
  },
  computed: {
    ...mapState(useAccountStore, ['profilePhotoUrl', 'adminEmail', 'caName', 'profilePhotoUrl']),
    ...mapState(useTemplateStore, ['template', 'components', 'excelTemplate', 'tid', 'templateName',
    //   'cardHeaders',
    ]),
    canvasStyle() {
      return this.importingCredentials
        ? 'max-height: 820px;'
        : 'max-height: 60vh;'
    },
  },
  beforeUnmount() {
    // this.$store.dispatch('setTemplate', '');
    this.resetTemplateValues()
  },
  created() {
    logDebug(`*** ${this.$options.name} ***`)

    this.initialLoad()
  },
  mounted() {
    // Determine if drag and drop functionality is capable in the browser
    this.dragAndDropCapable = this.determineDragAndDropCapable()

    if (this.dragAndDropCapable) {
      /*
      Listen to all of the drag events and bind an event listener to each
      for the fileform.
    */
      [
        'drag',
        'dragstart',
        'dragend',
        'dragover',
        'dragenter',
        'dragleave',
        'drop',
      ].forEach(
        (evt) => {
          this.$refs.fileform.addEventListener(
            evt,
            (e) => {
              e.preventDefault()
              e.stopPropagation()
            },
            false,
          )
        },
      )

      this.$refs.fileform.addEventListener(
        'drop',
        (e) => {
          const files = e.dataTransfer.files
          this.uploadFiles(files)
          // for (let i = 0; i < e.dataTransfer.files.length; i++) {
          //   this.$store.commit("ADD_FILE", e.dataTransfer.files);
          // }
          this.highlight = false
        },
      )
    }
  },
  methods: {
    async toDataURL(url) {
      if (url.startsWith('data:image')) {
        return url
      }

      return await new Promise((resolve) => {
        const xhr = new XMLHttpRequest()
        xhr.open('GET', `${url}?_=${Date.now()}`, true)
        xhr.onload = function () {
          const reader = new FileReader()
          reader.onloadend = function () {
            resolve(reader.result)
          }
          reader.readAsDataURL(xhr.response)
        }
        xhr.onerror = function () {
          resolve(undefined)
          console.error('** An error occurred during the XMLHttpRequest')
        }
        xhr.responseType = 'blob'
        xhr.send()
      })
    },
    async initialLoad() {
      if (this.customTemplateName === 'templateEditor') {
        logDebug('frontend_props', this.components)

        // this.templateCopy = JSON.parse(JSON.stringify(this.frontend_props))

        this.template.backgroundFront = await this.toDataURL(
          this.template.backgroundFront,
        )
        if (this.template.backgroundBack) {
          this.template.backgroundBack = await this.toDataURL(
            this.template.backgroundBack,
          )
        }
      }

      useTemplateStore().backSide = false
      useTemplateStore().setActiveComponent()

      for (const i in this.frontend_props.components) {
        if (this.frontend_props.components[i].templateSide === 'back') {
          if (this.pages.length === 1) {
            this.pages.push('#back')
          }

          this.$store.dispatch('enableBackSide', true)
        }
        if (this.frontend_props.components[i].type === 'image') {
          this.frontend_props.components[i].src = await this.toDataURL(
            this.frontend_props.components[i].src,
          )
        }
      }
    },
    resetTemplateValues() {
      logDebug('START reset FUNCTION in bulk')
      useTemplateStore().$reset()

      // this.$store.commit('template/tid', null)

      // this.$store.commit('template/frontend_props', this.templateCopy)

      // this.$store.dispatch('setTemplate', this.templateCopy)
    },
    async replaceAttributeText(component, cred) {
      // logDebug('START REPLACE FUNCTION');
      // logDebug(component);
      // logDebug(cred);

      // logDebug(component.baseText);
      let attrs = component.text.match(/\[[A-Z0-9_]*\]/g)

      // If the field already has a value we need to get the baseText previously set (1st value change)
      if (
        component.baseText
        && Array.isArray(component.baseText)
        && component.originalText
      ) {
        attrs = component.originalText.join(' ').match(/\[[A-Z0-9_]*\]/g)
      } else if (component.baseText) {
        attrs = component.baseText.match(/\[[A-Z0-9_]*\]/g)
      }

      // Search the attributes in text for the corrent one component.attr
      if (attrs && attrs.length > 0) {
        let finalText
        if (Array.isArray(component.baseText) && component.originalText) {
          finalText = component.originalText.join(' ')
        } else {
          finalText = component.baseText || component.text
        }

        for (let index = 0; index < attrs.length; index++) {
          let el = attrs[index]
          el = el.replace(/\[|\]/g, '')
          // logDebug('el', el);
          // logDebug(' attr', cred[el]);

          if (!component.baseText) {
            component.baseText = component.text
          }

          finalText = finalText.replace(`[${el}]`, cred[el])
        }

        component.text = finalText
        // logDebug('item.text', component.text);

        this.$store.dispatch('setTemplate', this.frontend_props)

        this.$forceUpdate()
      }
    },

    changeCredential(cred) {
      if (this.customTemplateName !== 'templateEditor') {
        this.parseCardValues({ user_data: cred })
      } else {
        if (this.frontend_props.backgroundBack) {
          if (this.pages.length === 1) {
            this.pages.push('#back')
          }

          this.$store.dispatch('enableBackSide', true)
        }
        for (const i in this.frontend_props.components) {
          if (this.frontend_props.components[i].templateSide === 'back') {
            if (this.pages.length === 1) {
              this.pages.push('#back')
            }

            this.$store.dispatch('enableBackSide', true)
          }

          // if (
          //   this.frontend_props.components[i].type === 'text'
          //   && this.isNFT()
          // ) {
          //   this.frontend_props.backgroundFront = cred.BACKGROUND
          // } else
          if (this.frontend_props.components[i].type === 'text') {
            // logDebug(this.components[i]);
            // .filter(function(el) {
            //   return el;
            // });
            this.replaceAttributeText(this.frontend_props.components[i], cred)
            // logDebug(this.components[i].baseText);
          }
        }
      }
    },
    downloadTemplate() {
      window.open(this.excelTemplate)
    },
    handleFileUpload() {
      this.uploadFiles(this.$refs.pdf.files)
      const input = this.$refs.pdf
      input.type = 'text'
      input.type = 'file'
    },
    back() {
      this.showConfirmData = false
      this.templateValues = []
      this.templateTable = []
      this.headers = []
      this.step = 0
      this.buttonText = this.$t('button.next')
    },
    next() {
      if (this.showConfirmData) {
        this.import()
      } else {
        this.upload()
      }
    },

    parseCardValues(user) {
      let index = 0
      this.templateValues = []

      this.tableValues = user.table_values
      const checkIfHeadersExist
        = this.headers.length === Object.entries(user.user_data).length
      for (const [key, value] of Object.entries(user.user_data)) {
        if (!checkIfHeadersExist) {
          this.headers.push({
            text: key,
            align: 'start',
            value: key,
            width: 150,
            sortable: false,
          })
        }
        this.templateValues.push({ attr: key, value, index })
        index++
      }
    },
    upload() {
      const formData = new FormData()

      formData.append('uploadfile', this.file)
      formData.append('tid', this.tid)

      this.$log.debug('upload: ', UPLOAD_DOC)

      this.$store
        .dispatch(UPLOAD_DOC, { formData })
        .then((res) => {
          // self.$router.push("/verify");
          this.import_data = res.data.data

          logDebug('tableValues', this.tableValues)

          // Create table headers (not card)
          let i = 0
          if (this.tableValues) {
            for (const t of this.tableValues) {
              for (const [key, value] of Object.entries(t)) {
                const el = {
                  text: key,
                  align: 'start',
                  value: key + i,
                  width: 150,
                }
                el[key] = value
                this.headers.push(el)
              }
              i++
            }
          }

          // Create Initial values for card

          setTimeout(() => {
            this.changeCredential(this.import_data[0].user_data)
          })

          logDebug('this.import_data', this.import_data)
          this.initialLoad()
          // Create table headers
          for (const [key] of Object.entries(
            this.import_data[0].user_data,
          )) {
            this.headers.push({
              text: key,
              align: 'start',
              value: key,
              width: 150,
              sortable: false,
            })
          }

          // Push the rest of users itens to table

          for (const e of this.import_data) {
            const entry = {}

            for (const [key, value] of Object.entries(e.user_data)) {
              entry[key] = value
            }

            let i = 0
            if (e.table_values) {
              for (const t of e.table_values) {
                for (const [key, value] of Object.entries(t)) {
                  entry[key + i] = value
                }

                i++
              }
            }
            logDebug(entry)
            this.templateTable.push(entry)
          }
          logDebug(this.templateTable)

          this.showConfirmData = true
          this.step = 1

          this.buttonText = this.$t('inBulkModal.buttonIssue')
        })
        .catch((err) => {
          console.error('Error Emiting File:', err)
          // TO DO :: remove file from list
          this.errorMsg = this.$t('inBulkModal.error')
        })
    },

    async import() {
      this.importingCredentials = true
      this.sending = true
      this.reload = false

      this.changeCredential(this.import_data[0].user_data)
      this.$nextTick(async () => {
        this.reload = true
        setTimeout(async () => {
          this.index = 1
          while (this.index <= this.import_data.length) {
            // The next credential is passed here and we store the result array in the
            // previous position
            for (
              let i = 0;
              i < this.import_data[this.index - 1].imgArray.length;
              i++
            ) {
              const el = this.import_data[this.index - 1].imgArray[i]
              logDebug(el)
            }
            this.index++
          }
          // this.importingCredentials = false;

          this.$log.debug('IMPORT_FILE', IMPORT_FILE)

          this.$store
            .dispatch(IMPORT_FILE, {
              tid: this.tid,
              import_data: this.import_data,
            })
            .then(() => {
              // self.$router.push("/verify");
              this.popupMessage = 'credentials'
              this.showSuccessModal = true
            })
            .catch((err) => {
              this.importing = false
              console.error('Error Emiting File:', err)
              this.errorMsg = this.$t('inBulkModal.error')
              // TO DO :: remove file from list
            })
        }, 0)
      })
    },
    uploadFiles(f) {
      this.errorMsg = null
      const loadFiles = (file) => {
        this.file = file
        setTimeout(() => {
          const wrapper = document.getElementById('scroll')
          if (wrapper) {
            wrapper.scrollBy({ top: 200, left: 0, behavior: 'smooth' })
          }
        }, 200)
      }

      for (let i = 0; i < f.length; i++) {
        logDebug(f[i])
        const ext = f[i].name.match(/\.([^.]+)$/)[1]
        switch (ext) {
          case 'csv':
            loadFiles(f[i])
            break
          default:
            console.error('Rejected file type ', f[i].name)
            this.errorMsg = this.$t('inBulkModal.errorFile')
        }
      }
    },
    determineDragAndDropCapable() {
      const div = document.createElement('div')

      return (
        ('draggable' in div || ('ondragstart' in div && 'ondrop' in div))
        && 'FormData' in window
        && 'FileReader' in window
      )
    },
    openFileSelection() {
      this.$refs.pdf.click()
    },
    onDragOver(event) {
      event.preventDefault()

      this.highlight = true
    },
    onDragLeave(event) {
      event.preventDefault()

      this.highlight = false
    },
    close() {
      this.$emit('close')
    },
  },

}
</script>

<template>
  <v-container class="issue-bulk px-0">
    <v-row>
      <v-col cols="6" class="">
        <h2 class="title_header pr-4">
          {{ $t('inBulkModal.title') }}
        </h2>
        <div class="steps">
          {{ $t('signup.step[0]') }} {{ step + 1 }} {{ $t('signup.step[1]') }} 2
        </div>
      </v-col>
      <v-col cols="6" class="text-right">
        <v-btn
          v-if="step === 0"
          class="advance-btn mr-5"
          @click="downloadTemplate()"
        >
          {{ $t('button.download') }}
        </v-btn>
        <v-btn v-if="step === 1" class="back mr-4" @click="back()">
          {{
            $t('button.back')
          }}
        </v-btn>
        <v-btn
          class="next"
          :disabled="!file"
          :loading="importingCredentials"
          @click="next()"
        >
          {{ buttonText }}
        </v-btn>
      </v-col>
    </v-row>
    <v-card flat class="pa-8 mt-6">
      <v-row v-if="!showConfirmData">
        <v-col cols="12" md="10" class="">
          <p class="subtitle_header mb-6">
            {{ $t('inBulkModal.text1') }}
          </p>
          <p class="subtitle_text mb-0" style="max-width: 800px">
            {{ $t('inBulkModal.text2') }}
          </p>
        </v-col>
        <v-col id="file-drag-drop" cols="12" class="">
          <input
            id="pdf"
            ref="pdf"
            type="file"
            style="visibility: hidden; width: 0px; height: 0px"
            accept=".csv"
            multiple="multiple"
            @change="handleFileUpload()"
          />

          <div
            class="outside-box"
            :class="{ 'error-upload': errorMsg }"
            @click="openFileSelection"
          >
            <form
              ref="fileform"
              class="drag-drop-form"
              :class="{ highlight }"
              style="cursor: pointer"
              @dragover="onDragOver"
              @dragleave="onDragLeave"
            >
              <div style="display: flex">
                <IconUpload class="mx-auto" />
              </div>
              <div>
                <p class="drop-files drag-box-text mx-auto my-0">
                  {{ $t('inBulkModal.upload[0]') }}
                  <label style="cursor: pointer" class="browse-file">{{
                    $t('inBulkModal.upload[1]')
                  }}</label>
                  {{ $t('inBulkModal.upload[2]') }}
                </p>
              </div>
            </form>
          </div>

          <p v-if="errorMsg" class="light_text text-center mt-2 error-upload">
            {{ errorMsg }}
          </p>
          <p v-else class="light_text text-center mt-2">
            {{ $t('inBulkModal.hint') }}
          </p>
        </v-col>

        <v-col v-if="file" cols="12" class="pt-4">
          <div class="file-listing">
            <IconFile class="mr-2" />
            <p>{{ file.name }}</p>
            <v-spacer />
            <button
              class="btn-close"
              aria-label="Close modal"
              @click="file = null"
            >
              <IconClose />
            </button>
          </div>
        </v-col>
      </v-row>
      <v-row v-else>
        <v-col cols="12" md="10" class="pb-0">
          <p class="subtitle_text_strong" style="max-width: 580px">
            {{ $t('inBulkModal.text3') }}
          </p>
        </v-col>

        <v-col cols="12" md="5" class="pb-1">
          <v-data-table
            class="table_in_bulk"
            :headers="headers"
            :items="templateTable"
            :no-results-text="$t('table.noData')"
            :items-per-page="10"
            :hide-default-footer="templateTable.length < 10"
          >
            <template #body="{ items }">
              <tbody>
                <tr
                  v-for="(row, indexR) in items"
                  :key="indexR"
                  @mouseover="!importingCredentials && changeCredential(row)"
                >
                  <td v-for="(item, indexL) in row" :key="indexL">
                    <p>
                      {{ reduceText(item, 30)[0] }}
                      <v-tooltip
                        v-if="reduceText(item, 30).length > 1"
                        bottom
                        content-class="tooltip-desc"
                      >
                        <template #activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">
                            <MoreInfo />
                          </span>
                        </template>
                        <p>{{ item }}</p>
                      </v-tooltip>
                    </p>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-data-table>
        </v-col>
        <v-col
          v-if="customTemplateName !== 'templateEditor'"
          cols="12"
          md="7"
          class="pb-1"
        >
          <CustomCard
            :front-template="templateValues"
            :back-template="cardHeaders"
            :tableValues="tableValues"
            :template-values="templateValues"
            :frontend_props="frontend_props"
            :ca-name="caName"
            :credential-name="credName"
            :url-photo="urlPhoto"
            :height="267"
            :width="378"
          />
        </v-col>
        <v-col v-else cols="12" md="7" class="static_image pt-0">
          <TemplateCanvas
            v-if="reload"
            :editable="false"
            :width="sending ? 1080 : 360"
            :style="canvasStyle"
          />
        </v-col>
      </v-row>
    </v-card>
    <MessageModal
      v-if="importingCredentials"
      :message="popupMessage"
      @close="$router.go(-1)"
    />
  </v-container>
</template>

